import {Component, Injector, OnInit} from '@angular/core';
import DataSource from 'devextreme/data/data_source';
import {BaseComponent} from '../../../@calls/components/base/base.component';

@Component({
    selector: 'app-calls',
    templateUrl: './calls.component.html',
    styleUrls: ['./calls.component.scss']
})
export class CallsComponent extends BaseComponent implements OnInit {
    dataSource: any;
    isLoading = false;
    page = 0;
    itemsCount = 10;
    selected: any;
    rate: any = [];

    constructor(injector: Injector) {
        super(injector);
    }

    ngOnInit(): void {
        this.getCalls();
    }

    deleteItem(item): void {
        console.log(item);
        this.isLoading = true;
        this.callsService.removeCall(item.id).subscribe((res) => {
            console.log(res);
            this.getCalls();
            this.isLoading = false;
        }, (err) => {
            console.log(err);
            this.isLoading = false;
            this.utilService.showConnectionErrorToast();
        });
    }


    getCalls(): void {
        this.dataSource = new DataSource({
            key: 'id',
            load: (loadOptions) => this.callsService.getCalls(this.page, this.itemsCount).toPromise()
                .then(result => {
                    return {
                        data: result.content.data.data,
                        totalCount: result.content.data.total,
                        groupCount: result.content.data.per_page
                    };
                })
        });
    }

    viewDetails(item) {
        console.log(item.data);
        this.selected = item.data;
        this.rate = Array.from({length: +this.selected.rate}, (v, k) => k);
        document.getElementById('showModal').click();
    }

    getDate(date): string {
        const d = new Date(date);
        return d.toISOString().substring(0, 10);
    }

    getTime(date): string {
        const d = new Date(date);
        const p = d.toISOString().split('T');
        const three = p[1].substring(0, 5);
        return three.toString();
    }
}
